.hero {
  &__title {
    font-family: 'Film P3';
    font-weight: 700;
    font-size: 96px;
    line-height: 1.07;
    letter-spacing: 0.3em;
    color: var(--color-white);
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 24px;
    position: relative;
    left: 15px;

    @media screen and (min-width: 1024px) {
      font-size: 128px;
    }
  }

  &__subtitle {
    margin-bottom: 40px;
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: 0.02em;
    color: var(--color-white);
    text-align: center;

    @media screen and (min-width: 1024px) {
      font-size: 20px;
    }
  }

  &__slogan {
    font-family: 'Times New Roman';
    font-weight: 700;
    font-size: 32px;
    line-height: 1.15;
    letter-spacing: 0.05em;
    color: var(--color-white);
    text-align: center;
    margin-bottom: 113px;

    @media screen and (min-width: 1024px) {
      font-size: 36px;
      margin-bottom: 0;
    }
  }
  &__address {
    display: flex;
    width: 287px;
    padding: 8px 8px 8px 16px;
    border: 1px solid var(--color-accent-yellow);
    top: 64px;
    right: 15px;
    position: absolute;
    align-items: center;

    @media screen and (min-width: 1024px) {
      left: 47px;
      bottom: 64px;
      top: auto;
    }

    @media screen and (min-width: 1280px) {
      left: 85px;
    }
  }

  &__address-icon {
    fill: var(--color-accent-yellow);
    margin-right: 16px;
    flex-shrink: 0;
  }

  &__address-link {
    font-family: 'Montserrat';
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: 0.01em;
    color: var(--color-white);
    white-space: nowrap;
    text-decoration: none;
  }

  &__button-list {
    display: flex;
    flex-direction: column;
    gap: 24px;
    align-items: center;

    @media screen and (min-width: 1024px) {
      flex-direction: row;
      gap: 40px;
      position: absolute;
      right: 47px;
      bottom: 64px;
    }

    @media screen and (min-width: 1280px) {
      gap: 60px;
      right: 85px;
      bottom: 65px;
    }
  }

  &__container {
    padding-top: 181px;
    padding-bottom: 102px;
    position: relative;
    background-color: var(--color-background);
    background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
      url(../images/main-page/mobile-background-hero-main.jpg);
    background-position: center;
    background-size: cover;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(
          rgba(0, 0, 0, 0.65),
          rgba(0, 0, 0, 0.65)
        ),
        url(../images/main-page/mobile-background-hero-main@2x.jpg);
    }

    @media screen and (min-width: 1024px) {
      padding-top: 188px;
      padding-bottom: 257px;
      background-image: linear-gradient(
          rgba(0, 0, 0, 0.65),
          rgba(0, 0, 0, 0.65)
        ),
        url(../images/main-page/tablet-background-hero-main.jpg);

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: linear-gradient(
            rgba(0, 0, 0, 0.65),
            rgba(0, 0, 0, 0.65)
          ),
          url(../images/main-page/tablet-background-hero-main@2x.jpg);
      }

      @media screen and (min-width: 1280px) {
        padding-top: 164px;
        background-image: linear-gradient(
            rgba(0, 0, 0, 0.65),
            rgba(0, 0, 0, 0.65)
          ),
          url(../images/main-page/desktop-background-hero-main.jpg);

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: linear-gradient(
              rgba(0, 0, 0, 0.65),
              rgba(0, 0, 0, 0.65)
            ),
            url(../images/main-page/desktop-background-hero-main@2x.jpg);
        }
      }
    }
  }
}
